<template>
  <div>
    <div class="seach_bg">
      <div class="seach">
        <ul class="t1">
          <li>
            <div class="title">选择地区 :</div>
            <div style="border-bottom: 1px dashed #dedede;float:left;padding-bottom:5px;">
              <ul>
                <li>
                  <div
                    :class="{active : seachData.address == ''}"
                    @click="seachData.address = ''"
                  >不限</div>
                </li>
                <li
                  v-for="(item, index) in seachList.areasList"
                  @click="seachData.address = item.value"
                  :key="index"
                >
                  <div :class="{active : seachData.address == item.value}">{{item.label}}</div>
                </li>
              </ul>
              <div class="more" @click="more($event)">
                <Icon
                  type="ios-arrow-down"
                  class="seachIcon"
                  v-if="seachList.areasList.length > 6"
                />
              </div>
              <div class="cl"></div>
            </div>
          </li>
          <li>
            <div class="title">资金金额 :</div>
            <div style="border-bottom: 1px dashed #dedede;float:left;padding-bottom:5px;">
              <ul>
                <li>
                  <div
                    :class="{active : seachData.min == '0' && seachData.max == '99999999' }"
                    @click="seachData.min = '0' ; seachData.max = '99999999'"
                  >不限</div>
                </li>
                <li
                  v-for="(item, index) in seachList.moneyList"
                  @click="seachData.min = item.min ; seachData.max = item.max"
                  :key="index"
                >
                  <div
                    :class="{active : seachData.min == item.min && seachData.max == item.max }"
                  >{{`${item.min}万 - ${item.max}万`}}</div>
                </li>
              </ul>
              <div class="more" @click="more($event)">
                <Icon
                  type="ios-arrow-down"
                  class="seachIcon"
                  v-if="seachList.moneyList.length > 6"
                />
              </div>
              <div class="cl"></div>
            </div>
          </li>
          <li>
            <div class="title">优质资金 :</div>
            <div style="border-bottom: 1px dashed #dedede;float:left;padding-bottom:5px;">
              <ul>
                <li>
                  <div
                    :class="{active : seachData.quality == '' }"
                    @click="seachData.quality = ''"
                  >不限</div>
                </li>
                <li
                  v-for="(item, index) in seachList.qualityList"
                  @click="seachData.quality = item.value;"
                  :key="index"
                >
                  <div :class="{active : seachData.quality == item.value }">{{item.name}}</div>
                </li>
              </ul>
              <div class="more" @click="more($event)">
                <Icon
                  type="ios-arrow-down"
                  class="seachIcon"
                  v-if="seachList.qualityList.length > 6"
                />
              </div>
              <div class="cl"></div>
            </div>
          </li>
          <li>
            <div class="title">领域范围 :</div>
            <div style="border-bottom: 1px dashed #dedede;float:left;padding-bottom:5px;">
              <ul>
                <li>
                  <div
                    :class="{active : seachData.territory == '' }"
                    @click="seachData.territory = ''"
                  >不限</div>
                </li>
                <li
                  v-for="(item, index) in seachList.territory"
                  @click="seachData.territory = item.id;"
                  :key="index"
                >
                  <div :class="{active : seachData.territory == item.id }">{{item.name}}</div>
                </li>
              </ul>
              <div class="more" @click="more($event)">
                <Icon
                  type="ios-arrow-down"
                  class="seachIcon"
                  v-if="seachList.territory.length > 6"
                />
              </div>
              <div class="cl"></div>
            </div>
          </li>
          <li>
            <div class="title">投资方式 :</div>
            <div style="border-bottom: 1px dashed #dedede;float:left;padding-bottom:5px;">
              <ul>
                <li>
                  <div :class="{active : seachData.way == '' }" @click="seachData.way = ''">不限</div>
                </li>
                <li
                  v-for="(item, index) in seachList.way"
                  @click="seachData.way = item.id;"
                  :key="index"
                >
                  <div :class="{active : seachData.way == item.id }">{{item.name}}</div>
                </li>
              </ul>
              <div class="more" @click="more($event)">
                <Icon type="ios-arrow-down" class="seachIcon" v-if="seachList.way.length > 6" />
              </div>
              <div class="cl"></div>
            </div>
          </li>
        </ul>
        <div class="cl"></div>
        <div>
          <br />
          <Button
            icon="ios-search"
            type="warning"
            @click="getData"
            style="margin-left:120px;float:left;"
          >搜 索</Button>
          <!-- <div style="width:300px;float:left;margin-top:15px;margin-left:10px;">共 100 个资金</div> -->
        </div>
        <div class="cl"></div>
      </div>
    </div>
    <br />
    <div class="seach_content">
      <div class="seach_title">
        <ul>
          <li style="float:left;font-size:16px;margin:0;">找到相关 {{seachData.pageTotal}} 个资金</li>
          <li
            @click="clickMenus('moneySort',seachData.moneySort == 1 ? '2' :  seachData.moneySort == 2 ? '1' : '2')"
          >
            融资金额
            <Icon
              :type="seachData.moneySort == 1 ? 'md-arrow-down' :  seachData.moneySort == 2 ? 'md-arrow-up' : 'ios-remove'"
            />
          </li>
          <li
            @click="clickMenus('timeSort',seachData.timeSort == 1 ? '2' :  seachData.timeSort == 2 ? '1' : '2')"
          >
            发布时间
            <Icon
              :type="seachData.timeSort == 1 ? 'md-arrow-down' : seachData.timeSort == 2 ? 'md-arrow-up' : 'ios-remove'"
            />
          </li>
          <li
            @click="clickMenus('synthesizeSort',seachData.synthesizeSort == 1 ? '2' :  seachData.synthesizeSort == 2 ? '1' : '2')"
          >
            综合排序
            <Icon
              :type="seachData.synthesizeSort == 1 ? 'md-arrow-down' : seachData.synthesizeSort == 2 ? 'md-arrow-up' : 'ios-remove'"
            />
          </li>
        </ul>
        <div class="cl"></div>
      </div>
      <Table :columns="columns1" :data="data">
        <template slot-scope="{ row }" slot="name">
          <div class="table_left">
            <img :src="imgUrl+row.logo" alt />
          </div>
          <div class="table_right">
            <p class="table_title">
              <a>{{row.institutions}}</a>
            </p>
            <p
              style="word-break: break-all;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 1;
            height:16px;
            overflow: hidden;"
            >{{row.dyf}}</p>
            <p
              style="word-break: break-all;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 1;
            height:16px;
            overflow: hidden;"
            >领域：{{row.territory}}</p>
          </div>
        </template>
        <template slot-scope="{ row }" slot="touzi">
          <div style="padding:10px 0px;">
            <p style="padding:5px;" v-for="(item, index) in row.investment.slice(0,3)" :key="index">
              <span v-if="item.id == ''">{{item.projectName}}</span>
              {{item.projectName}}
            </p>
          </div>
        </template>
        <template slot-scope="{ row }" slot="money">{{row.money}}万</template>
        <template slot-scope="{ row }" slot="investment">{{row.investment.length}}次</template>
        <template slot-scope="{ row }" slot="createTime">{{row.createTime.split(' ')[0]}}</template>
        <template slot-scope="{ row }" slot="caozuo">
          <Button type="warning" ghost :to="`/capital/detail/${row.id}`">查看资金</Button>
        </template>
      </Table>
      <div class="page">
        <Page :total="seachData.pageTotal" @on-change="pageChange" show-elevator />
      </div>
    </div>
    <br />
    <br />
  </div>
</template>
<script>
import { Icon, Button, Table, Page } from "iview";
import { getFilter, getSortr } from "../../api/public";
import { areasList } from "../../areas-list";
import { imgUrl } from "../../utils";
export default {
  components: {
    [Button.name]: Button,
    [Table.name]: Table,
    [Page.name]: Page,
    [Icon.name]: Icon
  },
  data() {
    return {
      imgUrl: imgUrl,
      seachList: {
        areasList: areasList,
        moneyList: [
          {
            min: 0,
            max: 100
          },
          {
            min: 100,
            max: 300
          },
          {
            min: 300,
            max: 500
          },
          {
            min: 500,
            max: 1000
          },
          {
            min: 1000,
            max: 2000
          },
          {
            min: 2000,
            max: 5000
          },
          {
            min: 5000,
            max: 10000
          }
        ],
        qualityList: [
          {
            name: "优质项目",
            value: "Y"
          },
          {
            name: "普通项目",
            value: "N"
          }
        ],
        territory: [],
        way: []
      }, //搜索条件列表
      seachData: {
        type: "2", //资金方
        territory: "", //所属领域
        address: "", //所在地区
        way: "", //融资方式
        min: 0, //金额最小值
        max: 99999999, //金额最大值
        quality: "", //是否优质
        timeSort: "", //时间排序(1.降序 2.升序)
        moneySort: "", //金额排序(1.降序 2.升序)
        synthesizeSort: "1", //综合排序(1.降序 2.升序)
        pageNo: 1,
        size: 10,
        pageTotal: 0
      },
      columns1: [
        {
          title: "资金名称",
          slot: "name",
          width: 350
        },
        {
          title: "最近投资",
          slot: "touzi",
          width: 200
        },
        {
          title: "资产金额",
          slot: "money"
        },
        {
          title: "投资总数",
          slot: "investment",
          width:100
        },
        {
          title: "发布时间",
          slot: "createTime"
        },
        {
          title: "操作",
          slot: "caozuo",
          width: 120
        }
      ],
      data: []
    };
  },
  methods: {
    // 搜索查看更多
    more(e) {
      if (e.currentTarget.previousElementSibling.style.height == "auto") {
        e.currentTarget.previousElementSibling.style.height = "40px";
        e.currentTarget.childNodes[0].className =
          "seachIcon ivu-icon ivu-icon-ios-arrow-down";
      } else {
        e.currentTarget.previousElementSibling.style.height = "auto";
        e.currentTarget.childNodes[0].className =
          "seachIcon ivu-icon ivu-icon-ios-arrow-up";
      }
    },
    /**
     * @function 搜索导航三选一
     * @param {name seachData里面参数三选一 综合 发布 融资}
     * @param {val 改变的值}
     */
    clickMenus(name, val) {
      this.seachData.timeSort = "";
      this.seachData.moneySort = "";
      this.seachData.synthesizeSort = "";
      this.seachData[name] = val;
      this.getData();
    },
    //分页点击
    pageChange(page) {
      this.seachData.pageNo = page;
      this.getData();
    },
    // 获取数据
    getData() {
      getSortr(this.seachData).then(res => {
        this.data = res.data.data.rows;
        this.seachData.pageTotal = res.data.data.total;
      });
    }
  },
  created() {
    getFilter().then(res => {
      this.seachList = Object.assign(this.seachList, res.data.data);
    });
    this.getData();
  }
};
</script>
<style lang="less" scope>
.seach_bg {
  width: 100%;
  background: #ffffff;
  box-shadow: 10px 10px 13px #ececec;
}
.seachIcon {
  border: 1px solid #dcdee2;
  padding: 3px;
  border-radius: 3px;
  color: #b7b2b2;
}
.seach {
  width: 1024px;
  margin: 0 auto;
  padding: 20px 0px;
  font-size: 14px;
  margin-bottom: 20px;
  .t1 {
    li {
      float: left;
      margin-bottom: 10px;
      .title {
        width: 100px;
        text-align: left;
        float: left;
        line-height: 40px;
        font-weight: 400;
        font-size: 14px;
      }
      .more {
        width: 100px;
        text-align: center;
        float: right;
        line-height: 39px;
      }
      ul {
        width: 824px;
        float: left;
        height: 40px;
        overflow: hidden;
        li {
          width: 137px;
          float: left;
          padding-left: 20px;
          height: 40px;
          margin-bottom: 0px;
          div {
            padding: 8px 10px;
            text-align: center;
            margin-top: 4px;
            border-radius: 3px;
            white-space: nowrap;
            cursor: pointer;
          }
          .active {
            color: #f55231;
            background: #fff5e4;
          }
        }
      }
    }
  }
}
.seach_content {
  width: 1024px;
  margin: 0 auto;
  background: #ffffff;
  .seach_title {
    padding: 20px;
    border: 0.1px solid #dcdee2;
    border-bottom: none;
    ul {
      li {
        float: right;
        margin: 0px 10px;
        font-size: 14px;
        cursor: pointer;
      }
    }
  }
  .ivu-table {
    font-size: 14px;
    th {
      background-color: #ffffff;
      font-size: 16px;
      font-weight: 400;
      height: 57px;
    }
    .table_left {
      float: left;
      width: 66px;
      img {
        width: 46px;
        height: 46px;
        margin: 20px 0px;
      }
    }
    .table_right {
      float: left;
      width: 240px;
      padding: 15.5px 0px;
      p {
        font-size: 12px;
        width: 100%;
        float: left;
        margin-top: 5px;
        color: #777575;
      }
      .table_title {
        color: #000000;
        font-size: 16px;
      }
    }
  }
  .page {
    text-align: center;
    padding: 20px 0px;
    border: 1px solid #dcdee2;
    border-top: none;
  }
}
</style>